.header {
	padding-top: 40px;
	padding-bottom: 92px;
	background: url("../images/header-bg-phone.jpg") 0 0/ 100% 100% no-repeat;
	font-family: "Roboto", sans-serif;

	@media (min-width: 1280px) {
		padding-bottom: 84px;
		background-image: url("../images/header-bg.jpg");

		@media (min-device-pixel-ratio: 2),
		(-webkit-min-device-pixel-ratio: 2),
		(min-resolution: 192dpi),
		(min-resolution: 2dppx) {
			background-image: linear-gradient(rgba(47, 48, 58, 0.4), rgba(47, 48, 58, 0.4)),
				url('../images/header-bg@2x.jpg');
		}
	}

	@media (min-width: 768px) and (max-width: 1279px) {
		padding-bottom: 84px;
		background-image: url("../images/header-bg-tablet.jpg");

		@media (min-device-pixel-ratio: 2),
		(-webkit-min-device-pixel-ratio: 2),
		(min-resolution: 192dpi),
		(min-resolution: 2dppx) {
			background-image: linear-gradient(rgba(47, 48, 58, 0.4), rgba(47, 48, 58, 0.4)),
				url('../images/header-bg-tablet@2x.jpg');
		}
	}

	@media (max-width: 767px) {
		background-image: url("../images/header-bg-phone.jpg");

		@media (min-device-pixel-ratio: 2),
		(-webkit-min-device-pixel-ratio: 2),
		(min-resolution: 192dpi),
		(min-resolution: 2dppx) {
			background-image: linear-gradient(rgba(47, 48, 58, 0.4), rgba(47, 48, 58, 0.4)),
				url('../images/header-bg-phone@2x.jpg');
		}
	}
}

.headerforlibrary {
	padding-top: 40px;
	padding-bottom: 62px;
	background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
		url('/src/images/moblibrary1.jpg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;

	@media (-webkit-min-device-pixel-ratio: 2),
	(min-resolution: 192dpi),
	(min-resolution: 2dppx) {
		background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
			url('/src/images/moblibrary2@x.jpg');
	}



	@media screen and (min-width: 768px) {
		padding-bottom: 60px;
		background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
			url('/src/images/tabllibrary1.jpg');

		@media (-webkit-min-device-pixel-ratio: 2),
		(min-resolution: 192dpi),
		(min-resolution: 2dppx) {
			background-image: linear-gradient(rgba(0, 0, 0, 0.56),
					rgba(0, 0, 0, 0.56)),
				url('/src/images/tabllibrary@2x.jpg');
		}
	}

	@media screen and (min-width: 1280px) {
		background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
			url('/src/images/desclibrary1.jpg');

		@media (-webkit-min-device-pixel-ratio: 2),
		(min-resolution: 192dpi),
		(min-resolution: 2dppx) {
			background-image: linear-gradient(rgba(0, 0, 0, 0.56),
					rgba(0, 0, 0, 0.56)),
				url('/src/images/desclibrary@2x.jpg');
		}
	}

}



.header-container {

	align-items: center;
	justify-content: space-between;
}

.container-navigation{
	justify-content: space-between;
}



.logo__images{

	animation: 1700ms linear 0s normal none infinite running rot;
			-webkit-animation: 1700ms linear 0s normal none infinite running rot;
	
		}
		

@keyframes rot {
			0% {
				transform: rotateY(0deg);
				
			}
	
			100% {
				transform: rotateY(360deg);
			}
		}
	
@-webkit-keyframes rot {
			0% {
				transform: rotateY(0deg);
			}
	
			100% {
				transform: rotateY(360deg);
			}
}


.header-menu {
	display: flex;
	margin-bottom: 54px;
	line-height: 1.17;
    
	@media screen and (min-width: 768px) {
			margin-bottom: 40px;
		}


}
.header-menu_library{
	display: flex;
	margin-bottom: 60px;

	@media screen and (min-width: 768px) {
			margin-bottom: 40px;
		}

}


.header-menu__logo {
	display: flex;
	align-items: center;
	margin-right: auto;
	// z-index: 1;
}

.menu-logo__logo {
	width: 24px;
	height: 24px;
	margin-right: 8px;

	@media screen and (min-width: 1280px) {
			margin-right: 10px;
		}
}

.menu-logo__text {
	display: contents;
	z-index: 2;

	@media screen and (max-width: 767px) {
		display: none;
	}

	font-size: 30px;
	color: var(--secondary-white-text-color);
	margin: 0 0;
	
}

.header-menu__logo:hover,
.header-menu__logo:focus {
	.logo-text__animation {
		opacity: 0.1;
		animation-name: glowing;
		animation-duration: 500ms;
		animation-iteration-count: infinite;
	}
}

@keyframes glowing {
	0% {
		opacity: 1;
		filter: blur(1px);
		text-shadow: 0 0 10px rgba(255, 107, 8, 1), 0 0 20px rgba(255, 107, 8, 1), 0 0 40px rgba(255, 107, 8, 1),
			0 0 80px rgba(255, 107, 8, 1), 0 0 160px rgba(255, 107, 8, 1), 0 0 320px rgba(255, 107, 8, 1),
			0 0 640px rgba(255, 107, 8, 1);
	}

	100% {
		opacity: 1;
		filter: blur(0);
		text-shadow: none;
	}
}

.menu-logo__text {
	display: contents;

	@media screen and (max-width: 767px) {
		display: none;
	}
    font-weight: 500;
	font-size: 30px;
	color: var(--secondary-white-text-color);
	margin: 0 0;
	line-height: 1.17;
}

.header-menu__nav {
	display: flex;
}

.menu-nav__link {
	&:not(:last-child) {
		margin-right: 39px;
	}
}

.nav-link__item {
	position: relative;
	line-height: 1.17;
	padding: 4px 0;
	font-size: 12px;
	color: var(--secondary-white-text-color);
	background-color: transparent;
	border: none;
	cursor: pointer;
}

.current {
	&::after {
		content: "";
		position: absolute;
		bottom: 0px;
		left: 0;
		height: 3px;
		width: 100%;
		background-color: var(--third-red-text-color);
		border-radius: 2px;
		transition: background-color, opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
	}
}

.header-search {
	position: relative;
}

.search-input {
	display: block;
	margin: 0 auto;
	border: none;
	font-weight: 400;
	line-height: 1.20;
	border-bottom: 0.5px solid gray;
	background: none;
	outline: none;
	color: var(--secondary-white-text-color);

	&:focus {
		border-bottom: 0.5px solid var(--secondary-white-text-color);

		&::placeholder {
			color: var(--secondary-white-text-color)
		}
	}

	@media screen and (max-width: 767px) {
		width: 100%;
	}
	@media screen and (min-width: 768px) and (max-width: 1279px) {
		width: 336px;
	}
	@media screen and (min-width: 1280px) {
		width: 394px;
	}
}

.search-input::placeholder {
	color: gray;
}

.search-icon {
	position: absolute;
	width: 15px;
	height: 15px;
	fill: gray;
	right: 0;
	top: 0;

	&:hover,
	&:focus {
		cursor: pointer;
	}

	@media screen and (min-width: 768px) and (max-width: 1279px) {
		right: 185px;
	}
	@media screen and (min-width: 1280px) {
		right: 410px;
	}
}
	

.header-search__container {
	display: flex;
	justify-content: center;
}

.unvisible {
	display: none;
}

.library-button {
	z-index: 999;
	font-size: 12px;
	min-width: 129px;
	padding: 13px 15px;
	line-height: 1.34;
	background-color: transparent;
	border: 1px solid var(--secondary-white-text-color);
	color: var(--secondary-white-text-color);
	border-radius: 5px;
	transition: box-shadow 250ms linear, 
	            background-color 250ms linear;
    cursor: pointer;


	&:hover, 
	&:focus{
		background-color: #E85303;
		box-shadow: 0px 0px 7px 1px rgba(255, 255, 255, 0.53)
		;
	}

	@media (min-width: 768px) {
		min-width: 152px;
		padding: 13px 15px;
		line-height: 1.33;
	}

	@media (min-width: 1280px) {
		min-width: 147px;
		padding: 13px 13px;
	}
}

.library-first {
	margin-right: 20px;

}


.active_btn{
	background-color: var(--accent-color);
	border: 1px solid var(--accent-color);
	color: var(--secondary-white-text-color);
}


.error-container{
	position: absolute;
margin: 0;
width: 100%;
align-items: center;
text-align: center;
margin-top: 20px;
top: -100 px;
left:0;

}

.request-paragraph {
	display: block;
	font-size: 14px;	
	line-height: 1.14;
}

.container_render_link{
	display: flex;
	align-items: center;
}